import { Global, ThemeProvider } from "@emotion/react";
import { makeStyles } from "@material-ui/core/styles";
import { Box } from "@react-yuki/ui";
import React from "react";
import paintingColors from "src/assets/images/painting_colors.jpg";
import Slider from "./Slider";
import { globalStyles, theme } from "./stylesConfig";

const useStyles = makeStyles((theme) => ({
  container: {
    padding: "72px 0",
    borderTop: "solid #bebdd4 5px",
    textAlign: "center",
    width: "100%",
    backgroundRepeat: "no-repeat",
    backgroundSize: "cover",
    backgroundPosition: "bottom",
    backgroundImage: `url(${paintingColors})`,
    boxShadow: "inset 0 0 30px #000000a8",
    display: "flex",
    justifyContent: "center",
  },
}));

const App = () => {
  const classes = useStyles();
  const params: any = {
    effect: "cube",
    grabCursor: true,
    cubeEffect: {
      shadow: true,
      slideShadows: true,
      shadowOffset: 20,
      shadowScale: 0.94,
    },
    pagination: {
      el: ".swiper-pagination",
      clickable: true,
    },
    autoplay: {
      delay: 2000,
      disableOnInteraction: true,
    },
    loop: true,
  };
  return (
    <ThemeProvider theme={theme}>
      <>
        <Global styles={globalStyles} />
        <div className={classes.container}>
          <Box p={4}>
            <Box>
              <Slider id="three-d-cube-effect" hasImage params={params} />
            </Box>
          </Box>
        </div>
      </>
    </ThemeProvider>
  );
};

export default App;
