import { makeStyles } from "@material-ui/core";
import React from "react";
import Swiper from "react-id-swiper";
import image1 from "src/assets/images/art1.jpg";
import image2 from "src/assets/images/art2.jpg";
import image3 from "src/assets/images/art3.jpg";
import image4 from "src/assets/images/art4.jpg";
import { fonts } from "src/assets/styles";

const useStyles = makeStyles((theme) => ({
  sliderRoot: {
    height: 600,
  },
  imageStyle: {
    height: 600,
    width: "100%",
    objectFit: "cover",
    position: "relative",
  },
  imageWrapper: {
    position: "relative",
    width: "100%",
    height: "100%",
  },
  imageTextWrapper: {
    position: "absolute",
    width: "100%",
    height: "100%",
    top: 0,
    left: 0,
  },
  imageTextContent: {
    position: "relative",
    textAlign: "center",
    width: "100%",
    height: "100%",
    boxShadow: "inset 0 0 30px #000000a8",
    borderTop: "solid #565656 5px",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
  },
  imageText: {
    position: "relative",
    textAlign: "center",
    width: "80%",
    height: "100%",
    color: "#fff",
    fontSize: 48,
    lineHeight: "54px",
    fontWeight: 400,
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    fontFamily: fonts.fontFed,
    textShadow: "0 8px 8px rgb(0 255 255)",
  },
}));

const OneImage = (props: any) => {
  const classes = useStyles();
  const { image, text } = props;

  return (
    <div className={classes.imageWrapper}>
      <img src={image} alt="" className={classes.imageStyle} />
      <div className={classes.imageTextWrapper}>
        <div className={classes.imageTextContent}>
          <div className={classes.imageText}>{text}</div>
        </div>
      </div>
    </div>
  );
};

const BackgroundSlider = () => {
  const params = {
    pagination: {
      el: ".swiper-pagination",
      clickable: true,
    },
    navigation: {
      nextEl: ".swiper-button-next",
      prevEl: ".swiper-button-prev",
    },
    autoplay: {
      delay: 5000,
      disableOnInteraction: true,
    },
    loop: true,
  };
  return (
    <Swiper {...params}>
      <div>
        <OneImage image={image2} text="Coming soon..." />
      </div>
      <div>
        <OneImage image={image3} text="Coming soon..." />
      </div>
      <div>
        <OneImage image={image4} text="Coming soon..." />
      </div>
      <div>
        <OneImage image={image1} text="Coming soon..." />
      </div>
    </Swiper>
  );
};
export default BackgroundSlider;

