import { makeStyles } from "@material-ui/core/styles";
import clsx from "clsx";
import React from "react";
import DiscordLogo from "src/assets/images/discord.svg";
import HeaderLogo from "src/assets/images/header_image.png";
import MediumLogo from "src/assets/images/medium.png";
import TwitterLogo from "src/assets/images/twitter.svg";
import { fonts } from "src/assets/styles";

const useStyles = makeStyles((theme) => ({
  baseFooterRoot: {
    padding: "48px 20px 64px",
    borderTop: "solid #498270 5px",
    backgroundImage:
      "linear-gradient(rgba(12, 241, 167, 1), rgba(216, 34, 253, 1))",
  },
  bigFooterRoot: {
    display: "flex",
    alignItems: "center",
    justifyContent: "space-between",
    flexDirection: "row",
  },

  smallFooterRoot: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    flexDirection: "column",
  },

  isForSmallScreen: {
    display: "none",
    [theme.breakpoints.down("sm")]: {
      display: "flex",
    },
  },
  isForBigScreen: {
    display: "flex",
    [theme.breakpoints.down("sm")]: {
      display: "none",
    },
  },

  footerIcon: { width: "300px" },
  footerText: {
    textAlign: "center",
    fontFamily: fonts.fontFed,
    color: "#fff",
    fontSize: 32,
    fontWeight: 400,
    padding: "32px 0",
  },
  footerSocialIcons: {
    display: "flex",
    alignItems: "center",
  },
  footerSocialItem: {
    marginLeft: 8,
    marginRight: 8,
  },
}));

const FooterComponents = (props: any) => {
  const classes = useStyles();

  return (
    <>
      <img src={HeaderLogo} alt="icon" className={classes.footerIcon} />
      <div className={classes.footerText}>Copyright © SOLANA PAINT</div>
      <div className={classes.footerSocialIcons}>
        <a
          href="https://twitter.com/SolanaPaint"
          className={classes.footerSocialItem}
          target="_blank"
          rel="noreferrer"
        >
          <img src={TwitterLogo} alt="icon" width="30" />
        </a>
        <a
          href="https://twitter.com/SolanaPaint"
          className={classes.footerSocialItem}
          target="_blank"
          rel="noreferrer"
        >
          <img src={MediumLogo} alt="icon" height="30" />
        </a>
        <a
          href="https://twitter.com/SolanaPaint"
          className={classes.footerSocialItem}
          target="_blank"
          rel="noreferrer"
        >
          <img src={DiscordLogo} alt="icon" width="30" />
        </a>
      </div>
    </>
  );
};

const Footer = (props: any) => {
  const classes = useStyles();

  return (
    <>
      <div
        className={clsx(
          classes.bigFooterRoot,
          classes.isForBigScreen,
          classes.baseFooterRoot
        )}
      >
        <FooterComponents />
      </div>
      <div
        className={clsx(
          classes.smallFooterRoot,
          classes.isForSmallScreen,
          classes.baseFooterRoot
        )}
      >
        <FooterComponents />
      </div>
    </>
  );
};

export default Footer;
